.CitySearch {
  margin-top: 20px;
}

p {
  color: #056983;
}
.suggestions {
  width: 250px;
  position: relative;
  z-index: 3;
  align-self: center;
  margin: 0;
  top: 10px;
  font-size: 14px;
  box-shadow: 10px 10px 16px -12px rgba(0, 0, 0, 0.75);
}

.suggestions > li {
  text-align: left;
  padding: 8px 6px 8px 10px;
  background-color: rgba(255, 255, 255, 0.945);
  cursor: default;
}

.suggestions > li:last-child {
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}

.suggestions > li:hover {
  background-color: #78b8d6;
}
.alert {
  margin-bottom: 20px;
}
