.event {
  margin-top: 10px;
  padding: 10px 10px 35px;
  margin-bottom: 20px;
  text-align: left;
  border: 1px solid #056983;
  border-radius: 4px;
  position: relative;
  background-color: #0190af;
}

#eventButton {
  background-color: #00485a !important;
  color: white !important;
  position: absolute;
  right: 5px;
  bottom: 5px;
  padding: 5px 8px;
  border-radius: 4px;
  outline: none;
  cursor: pointer;
}

.summary {
  color: white;
}

.event-body {
  background-color: white;
  color: #056983;
}

.location {
  font-size: small;
}

.about {
  font-weight: bolder;
}
