.name {
	font-size: 18px;
	font-weight: 600;
}

.group-name {
	text-transform: uppercase;
	font-size: 14px;
}

.going,
.visibility {
	font-style: italic;
}

.description {
	overflow-x: hidden;
}

.numberOfEvents {
	width: 200px;
	margin: 20px auto;
	text-align: center;
}

.button-wrapper {
	display: grid;
	grid-template-columns: 1fr 1fr 1fr;
	grid-gap: 20px;
	padding: 20px;
	font-family: sans-serif;
}

