.data-vis-wrapper {
	display: flex;
	align-items: center;
	flex-direction: column;
}
.data-vis-wrapper .recharts-responsive-container {
	min-width: 100%;
	display: flex;
}
.recharts-surface {
	overflow: visible;
}

.login-button {
	color: #494949;
	text-transform: uppercase;
	text-decoration: none;
	background: #ffffff;
	padding: 20px;
	border: 4px solid #494949;
	display: inline-block;
	transition: all 0.4s ease 0s;
	min-width: 10rem;
	margin: 1rem;
}

.google-btn {
	width: 184px;
	height: 42px;
	margin: 1rem;
	background-color: #4285f4;
	border-radius: 2px;
	box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);
}
.google-btn:hover {
	box-shadow: 0 0 6px #4285f4;
}
.google-btn:active {
	background: #1669f2;
}
.google-icon-wrapper {
	position: absolute;
	margin-top: 1px;
	margin-left: 1px;
	width: 40px;
	height: 40px;
	border-radius: 2px;
	background-color: #fff;
}
.google-icon {
	position: absolute;
	margin-top: 11px;
	margin-left: -8px;
	width: 18px;
	height: 18px;
}
.btn-text {
	float: right;
	margin: 11px 11px 0 0;
	color: #fff;
	font-size: 14px;
	letter-spacing: 0.2px;
	font-family: 'Roboto';
}

@media only screen and (min-width: 992px) {
	.data-vis-wrapper {
		flex-direction: row;
	}
	.data-vis-wrapper .recharts-responsive-container {
		flex-basis: 50%;
		max-width: 50%;
		min-width: auto;
		margin: 0 auto;
	}
}

